import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const state = {
  detailPageClose: false,
  isLoging: false,
  isNavBottomShow: true
}

const mutations = {
  toggleDetailPage (state) {
    state.detailPageClose = !state.detailPageClose
    console.log(state.detailPageClose)
  },
  login (state) {
    state.isLoging = true
  },
  exit (state) {
    state.isLoging = false
  },
  hideNavBottom (state) {
    state.isNavBottomShow = false
  },
  showNavBottom (state) {
    state.isNavBottomShow = true
  }
}

const getters = {}

export default new Vuex.Store({
  state,
  mutations,
  getters
})



